import React, { Component, useEffect, useMemo, useRef, useState } from 'react';
import { getPosts, getInfo } from './firebase';
import Masonry from 'react-smart-masonry';
import Hover from './Hover/Hover';
import { Link } from 'react-router-dom';
import Select from './Select/Select';
import '../Styles/Main.scss';
import { push } from 'firebase/database';

let postsDOM = [];

export default function Main(props) {
  const isEdit = props.isEdit;
  const [value, setValue] = useState(0);
  const [scroll, setScroll] = useState(0);

  useEffect(()=>{
    window.dispatchEvent(new Event("resize"));
  })

  let imagesLoaded = 0;
  
  const location = "/Main"
  const [images, setImages] = useState([]);
  const [selectedId, setSelectedId] = useState(0);
  const [posts, setPosts] = useState({});

  let masonryRef = useRef(null);

  let postsRef = useRef([]);
  postsRef.current = [];
  
  const addToRefs = (el) => {
    if(el && postsRef.current.filter(post=>post.href === el.href).length === 0){
      postsRef.current.push(el);
    }
  }

  useMemo(()=>{
    getPosts(location, (images)=>{
      setImages(images
        .filter(image=>image)
        .sort((image1, image2)=>{
        return image1.order - image2.order;
      }));
    })
  },[value])

  useMemo(()=>{
    getInfo("/Posts", (posts)=>{
      setPosts(posts);
    });
  },[value])

  const postsList = Object.values(posts).map((post, index)=>{
    return (
      <Link
        className="post-preview"
        to={`/${post.Name}`}
        key={index}
        ref={addToRefs}
        test={1}
        >
        <img className="post-preview__image" onLoad={()=>{
          imagesLoaded++;
          if(imagesLoaded <= Object.values(images).length){
            window.dispatchEvent(new Event("resize"));
          }
          }} src={post.MainImage} alt={`main ${post.Name}`} />
          <h2 className="post-preview__name">
            {post.Name}
          </h2>
      </Link>)
  })

  const setPostsArray = () => {
    Array.from(masonryRef.current.children).forEach(div=>{
      Array.from(div.children).forEach(post=>{
        postsDOM.push(post);
      });
    })
  }

  // document.addEventListener('load', function (e) {
  //   Array.from(masonryRef.current.children).forEach(div=>{
  //     Array.from(div.children).forEach(post=>{
  //       postsDOM.push(post);
  //     });
  //   })
  // });

  useEffect(()=>{
    setTimeout(function() {
      setPostsArray()
    }, 1000);
  },[postsList])
  
  window.addEventListener("scroll", (e)=>{
    if(postsDOM.length > 0){
      postsDOM.forEach(post=>{
        let offset =(post.getBoundingClientRect().top - window.innerHeight/2)
        post.firstElementChild.style.transform = `translateY(${offset / 20}%)`;
      })
    }
  })
  

  return (
    <section className='main'>
      <div className="main__container container">
        {/* <Select callback={()=>{setValue(value+1)}} description="hidden" isEdit={isEdit} location={location} selectedId={selectedId} images={images}/> */}
        <Masonry 
          className="main__masonry masonry"
          columns={{phone: 1, tablet: 2, desktop: 3}}
          breakpoints={{tablet: 681, desktop: 769}} 
          gap={50}
          autoArrange={true}
          ref={masonryRef}
          >
          {
            postsList
          }
        </Masonry>
      </div>
    </section>
  )
}