import React, { useEffect, useMemo, useState } from 'react'
import { useParams } from 'react-router-dom';
import { getInfo } from './firebase';

export default function Post() {
  const {title} = useParams();
  const [post, setPost] = useState({});
  const [value, setValue] = useState(0);

  useMemo(()=>{
    getInfo(`/Posts/`, (posts)=>{
      setPost(Object.values(posts).filter(post=>{
        return post.Name === title
      })[0]);
    });
  },[value])

  return (
    Object.keys(post).length === 0
    ?
    <h1>loading...</h1>
    :
    <article className='post'>
      <div className="post__container">
        <div className="post__image-wrapper">
          <div className="post__main-image-block">
            <img className='post__main-image' src={ post.MainImage } alt="Main" />
          </div>
          <div className="post__image-block">
          {
            Object.values(post.Images).map((image, index)=>{
              return <img className='post__image' width={600} key={index} src={ image } alt="" />
            })
          }
        </div>
        </div>
        <div className="post__text-block">
          <h2 className="post__name">
            {post.Name}
          </h2>
          <p className="post__description">
            {post.Description}
          </p>
        </div>
      </div>
    </article>
  )
}
